@font-face {
  font-family: "Sansation Regular";
  src: url("../../fonts/sansation-regular.ttf");
}

:root {
  --preview-img-height: 100px;
}

.projects-wrapper {
  overflow: hidden;
  background: #191c4d;
}

.projects-wrapper img {
  pointer-events: none;
  user-select: none;
}

.projects-wrapper .container {
  margin-top: -5vh;
}

.projects__title {
  text-align: center;
  margin-bottom: 15px;
}

.title-wrapper {
  display: inline;
  position: relative;
}

.title-wrapper_line {
  display: block;
  position: absolute;
  bottom: -10px;
  left: 0;
  border-bottom: 2px solid #e2001c;
  width: 25%;
  transition: all ease .7s;
  -webkit-box-shadow: 0px 2px 4px 1px rgba(255,164,164,0.5);
  -moz-box-shadow: 0px 2px 4px 1px rgba(255,164,164,0.5);
  box-shadow: 0px 2px 4px 1px rgba(255,164,164,0.5);
}


.title-wrapper:hover .title-wrapper_line {
  width: 100%;
}

.projects__title h3 {
  text-transform: uppercase;
  color: white;
  font-size: 5vh;
  font-family: 'Sansation Regular', sans-serif;
  display: inline;
}

.nav-link {
  font-family: 'Sansation Regular', sans-serif;
  font-size: 2.5vh;
  color: white;
  cursor: pointer;
}

.active {
  color: #e2001c !important;
  text-shadow: 0 4px 5px #e40025;
}

.nav-link:hover {
  color: #e2001c;
  text-shadow: 0 4px 5px #e40025;
}

.projects__slider {
  position: relative;
}

.projects__slider:before,
.projects__slider:after {
  position: absolute;
  display: block;
  content: '';
  width: 10%;
  top: 47%;
  border-bottom: 2px solid #e2001c;
  -webkit-box-shadow: 0px 2px 4px 1px rgba(255,164,164,0.5);
  -moz-box-shadow: 0px 2px 4px 1px rgba(255,164,164,0.5);
  box-shadow: 0px 2px 4px 1px rgba(255,164,164,0.5);
}

.projects__slider:before {
  left: -12%;
}

.projects__slider:after {
  right: -12%;
}

.project__item {
  padding: 0 5%;
  margin: 1.5vh 0;
  height: 100%;
}

.project__item_date {
  padding: 7px;
  background: rgba(0, 0, 0, 0.66);
  text-align: left !important;
  margin: 0;
  text-align: center;
  color: #7b7b7b;
  font-size: 18px;
  font-family: 'Sansation Regular', sans-serif;
}

.project__item_image {
  position: relative;
  width: 100%;
  height: var(--preview-img-height);
}

.project__item_image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project__item_title {
  transform: translate(-50%, -50%);
  position: absolute; 
  top: 50%; 
  left: 50%;
  background: rgba(0, 0, 0, 0.75);
  width: 100%;
  text-align: center;
}

.project__item_title h4 {
  margin: 5px;
  font-size: 22px;
  color: white;
}

.project__item_title h4:hover{
  color: #e2001c;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  .project__item_title h4 {
    font-size: 18px;
  }
}

@media screen and (max-width: 767px) {
  .project__item_title h4 {
    font-size: 14px;
  }
  .project__item_date {
    white-space: nowrap;
    font-size: 14px;
  }
}

.button__bar {
  margin-top: 1vh;
  text-align: center;
  padding: 0;
}

.button__bar li {
  list-style: none;
  display: inline-block;
  padding: 0;
}

.button__bar li button {
  box-sizing: content-box;
  border: 2px solid #ff0000;
  background: inherit;
  color: transparent;
  cursor: pointer;
  display: block;
  height: 12px !important;
  width: 12px !important;
  border-radius: 50%;
  margin: 0 18px;
  padding: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.button__bar li.slick-active button {
  background-color: rgba(255, 0, 0, 0.62);
}

.button__bar li button:hover {
  background: rgba(255, 0, 0, 0.62);
  outline: 0;
}

.button__bar li button:focus{
    outline: none;
}
